
.discovery-card-list {
    display: flex;
    margin: 8px;
}
.pt-4, .py-4 {
    padding-top: 1.5rem!important;
}
.mt-4, .my-4 {
    margin-top: 1.5rem!important;
}
@media (min-width: 576px) {
.card-columns {
    -webkit-column-count: 4;
    column-count: 4;
    -webkit-column-gap: 0;
    -moz-column-gap: 0;
    column-gap: 0;
    orphans: 1;
    widows: 1;
}
}
.text-center {
    text-align: center!important;
}
.px-3 {
    color: #003459;
}
